.seemore{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 0px;
    height:40px;
}

.seemore>button{
    color: white;
    background-color: var(--desiredblue);
    border: none;
    border-radius: 5px;
    margin-right: 17px;
    padding: 4px 7px;
}

.news{
    display: flex;
    gap: 1rem;
}

.news>div{
    flex: 1;
    background-color: silver;
    border-radius: 1rem;
    height: 380px;
}

.threebtns{
    margin-top: 10px;
    margin-bottom: 40px;
    display: flex;
    justify-content: center;
}

.threebtns>button{
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: none;
    margin: 10px;
    background-color: #D9D9D9;
}

.tops{
    display: flex;
    margin-bottom: 30px;
    gap: 1rem;
}

.tops>div{
    flex: 1;
    height: 270px;
    background-color: silver;
    border-radius: 1rem;
}

.hometable{
    width: 100%;
    height: 800px;
}
