.seemore{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 17px;
    height:40px;
}

.seemore>button{
    color: white;
    background-color: var(--desiredblue);
    border: none;
    border-radius: 5px;
    margin-right: 17px;
    padding: 4px 7px;
}

.container1{
    display: flex;
    gap: 1rem;
    /* height: 400px; */
    /* background-color: silver; */
}

.container1>div:first-child{
    flex: 2;
}

.container1>div:last-child{
    flex: 1;
}



.container2{
    display: flex;
    margin: 30px 0;
    height: 500px;
    gap: 1rem;
}

.container2>div{
    flex: 1;
}

.container3{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 17px;
    margin-bottom: 40px;
}

.container3>div{
    /* box-shadow: var(--generalshadow); */
    /* height: 450px; */
}

.allarticles{
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}

.allarticles>button{
    color: white;
    background-color: var(--desiredblue);
    border: none;
    border-radius: 5px;
    padding: 5px 40px;
}