.container{
    position:fixed;
    display: flex;
    background-color: white;
    height: 80px;
    width: 100%;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
    align-items: center;
    font-size: 1rem;
    z-index: 10;
}

.logo{
    color: var(--desiredblue);
    width: 150px;
    text-align: center;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: large;
    font-weight: bold;
}

.cryptobtn{
    all: unset;
    /* position: relative; */
    cursor: pointer;
    margin: 10px;
}



.menucontainer{
    position: absolute;
    width: 250px;
    height: 300px;
    background-color: white;
    box-shadow: var(--generalshadow);
    border-radius: 7px;
    padding: 7px;
    display: flex;
    flex-direction: column;
    opacity: 0;
    pointer-events: none;
    top: 60px;
    transform: translateY(-25px);
    transition: 0.5s;
}

.menuopened{
   opacity: 1; 
    transform: translateY(0);
    pointer-events:auto;
}

.cryptosearch{
    padding-left: 10px;
    height: 40px;
    border-radius: 7px;
    background-color: rgb(240, 240, 240);
    font-size: 14px;
    outline: none;
    border: none;
}

.menu{
    height: 100%;
    background-color: rgb(255, 255, 255);
    border-radius: 7px;
    padding: 7px;
    overflow-y: scroll;
}

.menu::-webkit-scrollbar{
    width: 4px;
    background-color: rgb(209, 209, 209);
}

.menu::-webkit-scrollbar-thumb{
    background-color: gray;
    border-radius: 6px;
}


.menu>div{
    height: 50px;
    /* box-shadow: var(--generalshadow); */
    background-color: rgb(250, 250, 250);
    margin-bottom: 4px;
    border-radius: 5px;
}

.seemore{
    text-decoration: none;
    margin-top: auto;
}

.link{
    text-decoration: none;
    color: black;
    margin: 20px;
}

.themetoggle{
    margin-left: auto;
    display: flex;
    background-color: white;
    width: 90px;
    justify-content: space-between;
    height: 32px;
    border-radius: 30px;
    border: 1px solid var(--desiredblue);
    margin-right: 15px;
    position: relative;
    display: flex;
    align-items: center;
}

.themetoggle>span{
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    z-index: 2;
    pointer-events: none;
    font-size: 1rem;
}

.themetoggle>button{
    position: absolute;
    height: 100%;
    border-radius: 15px;
    width: 58%;
    background-color: var(--desiredblue);
    border: none;
    z-index: 1;
    cursor: pointer;
}

.darktoggled{
    background-color: rgb(236, 236, 236);
    right: 0;
}

.languageselect{
    -webkit-appearance: none;
    outline: none;
    margin: 10px;
    height: 47px;
    width: 119px;
    border-radius: 7px;
    cursor: pointer;
    font-size: 1rem;
    text-align: center;
}

.signup{
    font-size: 1rem;
    all: unset;
    border: var(--solidone);
    height: 47px;
    width: 119px;
    margin: 15px;
    text-align: center;
    border-radius: 10px;
    background-color: var(--desiredblue);
    color: white;
    cursor: pointer;
    border: none;
}