.header{
    position: absolute;
    left: 0;
    width: 100%;
    height: 66px;
    background-color: white;
    display: flex;
    align-items: center;
    gap: 2rem;
    box-shadow: var(--generalshadow);
    padding: 0 0.5rem;
}

.header div{
    display: flex;
    align-items: center;
    gap: 0.7rem;
}
.header img{
    width: 2rem;
    height: 2rem;
    border-radius: 1rem;
}

.container{
    display: flex;
    gap: 1rem;
    margin-bottom: 5rem;
    /* height: 560px; */
}

.container>div:first-child{
    flex: 6;
    border-radius: 0.5rem;
    overflow: hidden;
    height: 560px;
}


.container>div:last-child{
    flex: 2;
    overflow-y: scroll;
    background-color: white;
    border-radius: 0.5rem;
    box-shadow: var(--generalshadow);
    padding: 0.1rem 0.3rem;
    height: 530px;

}

.container>div:last-child::-webkit-scrollbar{
    width: 0px;
    background-color: rgb(223, 223, 223);
}