.container1{
    display: flex;
    gap: 5rem;
    margin: 30px 0;
    height: 350px;
}

.container1>div:first-child{
    flex: 1;
}


.container1>div:last-child{
    flex: 2;
}

.container2{
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
    margin-top: 1.5rem;
    border-bottom: 1px solid silver;
    padding-bottom: 1rem;
}

.container2>button{
    background: none;
    border: none;
    border-radius: 15px;
    padding: 12px 24px;
}

.container2>button:first-child{
    background-color: var(--desiredgreen);
    color: white;
}

.container3{
    display:flex;
    align-items:flex-start;
    gap: 1rem;
    margin-bottom: 10rem;
}


.column1{
    flex: 2;
}

.column1>div:first-child{
    height: 500px;
}

.column1>p{
    margin: 2rem 0 ;
    overflow:hidden;
    text-overflow: ellipsis;
    display:-webkit-box;
    -webkit-line-clamp:6;
    -webkit-box-orient:vertical;
    padding-right: 0.7rem;
    line-height: 1.8rem;
}

.column1>h3{
    margin-left: 0.3rem;
}

.column1>button{
    color: var(--desiredblue);
    background: none;
    border: none;
    margin-left: 1.5rem;
}

.converter{
    margin: 1.5rem 0 2.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    background-color: white;
    border-radius: 0.8rem;
    border: 1px solid #BEBEBE;
}

.converter>div:first-child{
    display: flex;
    align-items: center;
    gap: 1rem;
    width: 100%;
    padding-right: 1rem;
}

.converter>div:last-child{
    display: flex;
    align-items: center;
    gap: 1rem;
}

.converter>div:nth-child(2)>span{
    background-color: #EFF2F5;
    padding: 1rem;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.converter>div:last-child{
    width: 100%;
    padding-left: 1rem;
}

.converter>div:last-child>span:nth-child(2){
    font-size: 0.9rem;
}

.converter>div:last-child>span:nth-child(1){
    background-color: #EFF2F5;
    height: 1rem;
    width: 1rem;
    color: var(--desiredgreen);
    font-size: 0.8rem;
    font-weight: bold;
    box-sizing:content-box;
    padding: 0.3rem;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;    
}

.converter>div:last-child>span:last-child{
    margin-left: auto;
}

.graywrap{
    font-size: 0.7rem;
    padding: 0.2rem 0.4rem;
    border-radius: 0.6rem;
    background-color: #EFF2F5;
}

.greenwrap{
    font-size: 0.7rem;
    padding: 0.2rem 0.4rem;
    border-radius: 0.6rem;
    background-color: var(--desiredgreen);
}

.column2{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}


.background{
    background-color: white;
    padding: 1rem 0.4rem;
    border-radius: 0.7rem;
    display: flex;
    flex-direction: column;
    /* gap: 1.5rem; */
}

.background h3{
    margin-left: 0.4rem;
}

.background button{
    background: none;
    padding: 0.6rem;
    border-radius: 0.6rem;
    color: var(--desiredblue);
    border-color: var(--desiredblue);
}

.seemore{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 0px;
    height:40px;
}

.seemore>div{
    display: flex;
    gap: 1rem;
    align-items: center;
}

.seemore>div>div{
    background-color: #EFF2F5;
    display: flex;
    align-items: center;
    padding: 0.3rem;
    border-radius: 0.6rem;
    gap: 0.4rem;
}

.seemore>div>div>button{
    background: none;
    border: none;
    padding: 0.5rem;
    border-radius: 0.5rem;
}

.seemore>div>div>button:nth-child(3){
    background-color: var(--desiredgreen);
    color: white;
}

.seemore>button{
    background: none;
    color: var(--desiredblue);
    border: none;
    border-radius: 5px;
    margin-right: 17px;
    padding: 4px 7px;
}

.container4{
    margin-top: 2rem;
    margin-bottom: 4rem;
}


.container5{
    display: flex;
    margin-top: 3rem;
    /* height: 700px; */
    /* background-color: gray; */
    gap: 1rem;
    margin-bottom: 5rem;
}

.container5>div:first-child{
    flex: 1;
    /* background-color: red; */
}

.container5>div:last-child{
    flex: 1;
    /* background-color: blue; */
}

.container6{
    height: 160px;
    display: flex;
    gap: 0.6rem;
    margin-bottom: 4rem;
}

.container6>div{
    flex: 1;
    background-color: white;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
    border-radius: 0.8rem;
    padding: 0.5rem;
}

.container6>div>div:first-child{
    display: flex;
    gap: 1rem;
}

.container6 img{
    width: 100%;
}

.column11{
    flex: 2;
}

.column22{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.datebtn{
    display: flex;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    background: none;
    color: var(--desiredblue);
    border-color: var(--desiredblue);
    border-radius: 0.7rem;
}

.column11>div:last-child{
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}

.column11>div:last-child>button{
    background: none;
    padding: 0.5rem 8rem;
    border-radius: 0.5rem;
}

.column111{
    flex: 2;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.column222{
    flex: 1;
}

.estimationgrid{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1rem;
}

.estimationgrid>div{
    padding: 0.8rem 0;
    font-size: 0.9rem;
    border-radius: 1rem;
    background-color: #EFF2F5;
    height: 180px;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
}
