.container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.title{
    padding-left: 0.5rem;
}

.rows{
    flex:1;
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
}

.row{
    display: flex;
    justify-content: space-between;
    padding-left: 0.5rem;
}


.sides{
    display: flex;
    align-items: center;
}