.container2{
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
    margin-top: 1.5rem;
    border-bottom: 1px solid silver;
    padding-bottom: 1rem;
}

.container2>button{
    background: none;
    border: none;
    border-radius: 15px;
    padding: 12px 24px;
}

.container2>button:first-child{
    background-color: var(--desiredgreen);
    color: white;
}

.buttonsfield{
    display: flex;
    margin-bottom: 1rem;
    gap: 1rem;
    width: 100%;
}

.buttonsfield>button{
    background-color: white;
    border: 1px solid rgb(199, 199, 199);
    padding: 10px;
    border-radius: 0.7rem;
}


.buttonsfield>button:nth-child(2){
    color: white;
    background-color: var(--desiredblue);
    margin-left: auto;
}

.buttonsfield>div>select{
    /* -webkit- */
    padding: 11.5px;
    background: none;
    border-radius: 1rem;
    color: grey;
    border: none;
    margin-left: 6px;
    outline: none; 
    border: 1px solid rgb(241, 241, 241);
}

.markettable{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    font-size: 13px;
}

hr{
    border: 1px solid rgb(231, 231, 231);
}

.tablecounter{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

.tablecounter>button{
    background-color: white;
    margin: 10px;
    border: none;
}


.graywrap{
    font-size: 0.7rem;
    padding: 0.4rem 1rem;
    border-radius: 0.6rem;
    background-color: #EFF2F5;
}

.greenwrap{
    font-size: 0.7rem;
    padding: 0.2rem 0.4rem;
    border-radius: 0.6rem;
    color: white;
    background-color: var(--desiredgreen);
}

.bluewrap{
    font-size: 0.7rem;
    padding: 0.4rem 1rem;
    border-radius: 0.6rem;
    color: white;
    background-color: var(--desiredblue);
}