.container{
    width: 100%;
    height: 100%;
    padding: 0.4rem;
    box-sizing: border-box;
    border-radius: 10px;
    background-color: white;
    box-shadow: var(--generalshadow);
    display: flex;
    flex-direction: column;
}

.container>img{
    width: 100%;
    /* height: 50%; */
    aspect-ratio: 1.4/1;
    border-radius: 10px;
}

.container>div{
    width: 100%;
}


.container>div:nth-child(2){
    margin-top: 1rem;
    border: none;
    line-height: 1.6rem;
}


.container>div:last-child{
    border-top: 1px solid #f3f3f3;
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    padding: 8px 0;
}

.container>div:last-child>button{
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    border: none;
    background: none;
}
