.container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.header{
    display: flex;
    align-items: center;
    margin: 1.5rem 0 1.5rem 0.8rem;
    gap: 0.5rem;
}

.header+div{
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.news{
    background-color: rgb(255, 255, 255);
    flex: 1;
    border-radius: 0.8rem;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
    display: flex;
    padding-top: 1rem;
}

.text{
    overflow:hidden;
    text-overflow: ellipsis;
    display:-webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient:vertical;
    padding-right: 0.7rem;
    font-size: 1rem;
}

.stats{
    display: flex;
    gap: 1.5rem;
    padding: 1rem 0.7rem 0.9rem 0;
}

.stats>button{
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    border: none;
    background: none;
}

.stats img{
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
}