.container{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    width: 100%;
    height: 100%;
}
.header{
    display: flex;
    padding: 1rem;
    background-color: #F9F9F9;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.1);
    border-radius: 1rem;
    font-size: 0.9rem;
}
.row{
    display: flex;
    padding: 1rem;
    background-color: #ffffff;
    filter: drop-shadow(0px 8px 24px rgba(0, 0, 0, 0.05));
    font-size: 0.9rem;
    border-radius: 1rem;
}
