.container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.row{
    display: flex;
    align-items: center;
    font-size: 1rem;
    margin-left: 0.5rem;
}

.row1{
    justify-content: space-between;
}

.row1>div:last-child{
    display: flex;
    gap: 1rem;
}

.row1>div:last-child>button{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 16px;
    border-radius: 0.7rem;
    border: none;
    background-color: var(--desiredblue);
    color: white;
}

.row2{
    gap: 1rem;
}

.row2>span{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.2rem 0.4rem;
    border-radius: 2rem;

}

.row3{
    gap: 1.5rem;
    margin-top: 1rem;
}

.row4{
    gap: 1rem;
    margin-top: 1rem;
}

.row4>span[data-line]{
    width: 217px;
    height: 0.6rem;
    background-color: #EFF2F5;
    border-radius: 1rem;
}

.row4>select{
    width: 60px;
    height: 27px;
    border: none;
    background-color: #EFF2F5;
    border-radius: 0.5rem;
    outline: none;
}

.row5{
    gap: 0.5rem;
    height: 50%;
    margin-left: 0;
    margin-top: auto;
}

.detailcard{
    height: 100%;
    flex: 1;
    background-color: white;
    border-radius: 0.5rem;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
    padding:  1rem;
    display: flex;
    font-size: 12px;
    flex-direction: column;
}

.detailcard>div:nth-child(2){
    display: flex;
    flex-direction: column;
    margin-top: 1rem;
    gap: 0.7rem;
}

.detailcard>div:last-child{
    display: flex;
    flex-direction: column;
    gap: 0.7rem;
    margin-top: auto;
    height: 3rem;
}

.detailcard>div:last-child>div{
    display: flex;
    justify-content: space-between;
}

 
.container span{
    display: block;
}

[data-arrow]{
    transform: translateY(2px);
    margin-left: 3px;
}

[data-info]{
    transform: translateY(2px);
    margin-left: 3px;
    font-weight:400;
}

