.container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 14px;
}

.row1{
    display: flex;
    justify-content: space-between;
}

.row1>div{
    display: flex;
    align-items: center;
    gap: 1rem;
}


.row2{
    display: flex;
    gap: 5px;
}

.row2>span{
    padding: 0.6rem 1.5rem;
    background-color: white;
    border-radius: 0.7rem;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
}

.row2>span:first-child{
    border: 1px solid #00AE77;
}

.row3{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 0.5rem;
    column-gap: 1rem;
}

.row3>button{
    background: none;
    border: 1px solid var(--desiredblue);
    padding: 0.6rem 1.5rem;
    border-radius: 0.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
}

.row4{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.row4>h4{
    font-size: 1rem;
}

.row4>div{
    display: flex;
    justify-content: space-between;
}

.row4>div>button{
    background-color: #EFF2F5;
    display: flex;
    border-radius: 2rem;
    border: none;
    font-size: 12px;
    padding: 0.5rem;
}

.row4>div>button:last-child{
    background-color: var(--desiredblue);
    color: white;
}