.container{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 1rem;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
}

.container>img{
    width: 100%;
    aspect-ratio: 1.5/1;
    border-radius: 1rem;
}

.container>div{
    padding: 1rem;
    padding-top: 0;
}

.container>div:nth-child(2){
    display: flex;
}

.stats{
    display: flex;
    justify-content: space-between;
    padding: 1rem 0.7rem 0.9rem 0;
    gap: 2rem;
}

.stats>button{
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    border: none;
    background: none;
}

.stats img{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
}

.text{
    overflow:hidden;
    text-overflow: ellipsis;
    display:-webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient:vertical;
}
