.container{
    display: flex;
    gap: 1rem;
}

.container>div{
    flex: 1;
    padding: 1rem 0.7rem;
    background-color: white;
    border-radius: 1rem;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
}

.select{
    width: 30rem;
    height: 3.5rem;
    font-size: 1rem;
    border-radius: 0.8rem;
    border: 1px solid rgb(226, 226, 226);
    box-shadow: var(--generalshadow);
}