.container{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
}

.container>a{
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    background-color: gray;
    overflow: hidden;
    z-index: 0;
}

.container>a>img{
    width: 100%;
    height: 100%;
}

.text{
    position: absolute;
    bottom: 4rem;
    left: 1rem;
    color: white;
}

.bottom{
    position: absolute;
    bottom: 0rem;
    left: 0rem;
    color: white;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.bottom>div:first-child{
    width: 50%;
    height: 4rem;
    padding-left: 1rem;
    display: flex;
    justify-content: space-between;
}


.bottom>div:first-child>button{
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    border: none;
    background: none;
    color: white;
}

.bottom>div:last-child{
    width: 20%;
    height: 4rem;
    padding-right: 1rem;
    display: flex;
    justify-content: right;
    align-items: center;
    gap: 0.5rem;
}


.bottom>div:last-child>button{
    width: 0.7rem;
    height: 0.7rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    border: none;
    background-color: white;
    border-radius: 50%;
}

.bottom img{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
}