.container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    font-size: 1rem;
    gap: 0.8rem;
}

.news{
    padding-top: 0.5rem;
    display: flex;
    gap: 1rem;
    background-color: white;
    border-radius: 0.8rem;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
}

.news>div:first-child{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 2.5rem;
}


.news>div:last-child{
    display: flex;
    flex-direction: column;
    /* background-color: red; */
    width: 100%;
}

.text{
    overflow:hidden;
    text-overflow: ellipsis;
    display:-webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient:vertical;
    padding-right: 0.7rem;
}

.stats{
    display: flex;
    justify-content: space-between;
    padding: 1rem 0.7rem 0.9rem 0;
}

.stats>button{
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    border: none;
    background: none;
}

.stats img{
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
}