.buttonsfield{
    display: flex;
    margin-bottom: 1rem;
    gap: 1rem;
}

.buttonsfield>button{
    background-color: white;
    border: 1px solid rgb(199, 199, 199);
    padding: 10px;
    border-radius: 1rem;
}

.buttonsfield>button:last-child{
    border: none;
    color: grey;
    background-color: rgb(236, 236, 236);
    padding: 9px 30px;
    border-radius: 7px;
}

.buttonsfield>div{
    margin-left: auto;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.buttonsfield>div>select{
    /* -webkit- */
    padding: 11.5px;
    border-radius: 7px;
    background-color: rgb(236, 236, 236);
    color: grey;
    border: none;
    margin-left: 6px;
    outline: none;
}

.markettable{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    font-size: 13px;
}

hr{
    border: 1px solid rgb(231, 231, 231);
}

.tablecounter{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

.tablecounter>button{
    background-color: white;
    margin: 10px;
    border: none;
}