.container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    font-size: 1rem;
    gap: 0.5rem;
}

.news{
    padding: 0.3rem;
    display: flex;
    gap: 1rem;
    background-color: white;
    border-radius: 1rem;
    box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05);
    height: 10rem;
}

.news>div:first-child img{
    height: 100%;
    aspect-ratio: 1.4/1;
    object-fit: cover;
    border-radius: 0.8rem;
}


.news>div:last-child{
    padding: 0.5rem 0;
    padding-right: 1rem;
    display: flex;
    flex-direction: column;
    /* background-color: red; */
    flex: 1;
}
.news>div:last-child>div:last-child{
    height: 40%;
}


.news>div:last-child>div:first-child{
    font-size: 1.12rem;
}

.text{
    overflow:hidden;
    text-overflow: ellipsis;
    display:-webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient:vertical;
}

