.container{
    width: 100%;
    height: 100%;
    padding: 4% 3%;
    box-sizing: border-box;
    border-radius: 10px;
    background-color: white;
    box-shadow: var(--generalshadow);
    display: flex;
    flex-direction: column;
}

.container div{
    display: flex;
    align-items: center;
}

.header{
    margin-bottom: 4%;
}

.header>div:first-child>div>h3{
    margin-left: 1rem;
}

.header>div:last-child{
    margin-left: auto;
}


.header>div:last-child>a{
    display: flex;
    align-items: center;
    color: var(--desiredblue);
    font-size: 1.1rem;
    margin-right: 1rem;
}

.row{
    padding: 1.1rem 0.5rem;
}

.row>div:first-child{
    gap: 1rem;
}

.row>div:last-child{
    margin-left: auto;
}