.container{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}

.container>img{
    width: 100%;
    height: 48%;
    /* height: 50%; */
    /* aspect-ratio: 2/1; */
    object-fit:cover;
    border-radius: 10px;
}

.container>div{
    width: 100%;
}


.container>div:nth-child(2){
    margin-top: 4%;
}

.container>div:nth-child(3){
    margin-top: 4%;
    overflow:hidden;
    text-overflow: ellipsis;
    display:-webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient:vertical;
    padding-right: 0.7rem;
}

.stats{
    display: flex;
    justify-content: space-between;
    padding: 0.8rem 0.5rem 0.8rem 0;
    margin-top: auto;
    border-top: 1px solid #f3f3f3;
}

.stats>button{
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    border: none;
    background: none;
}

.stats img{
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
}