.container{
    width: 100%;
    height: 100%;
    padding: 1rem 0.3rem;
    box-sizing: border-box;
    border-radius: 10px;
    background-color: white;
    box-shadow: var(--generalshadow);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.container>div:last-child{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.header{
    margin-bottom: 4%;
    display: flex;
}

.header>div>h3{
    margin-left: 0.5rem;
    color:#00AE77;
}


.header>div:last-child>a{
    display: flex;
    align-items: center;
    color: var(--desiredblue);
    font-size: 1.1rem;
    margin-right: 1rem;
}

.row{
    display: flex;
    padding: 1rem;
    height: 3rem;
    background-color: #F9F9F9;
    border-radius: 0.8rem;
}

.row>div:first-child{
    gap: 1rem;
    display: flex;
    align-items: center;
}

.row>div:last-child{
    margin-left: auto;

    display: flex;
    align-items: center;
}