html{
    height: 100%;
    width: 100%;
    margin: 0;
}

body{
    background-color: rgb(0, 0, 0);
    margin: 0;
    width: 100%;
    min-height: 100%;
    display: flex;
    font-size: 20px;
}

body>div{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FCFCFC;

}


button,select{
    cursor: pointer;
}

*{
    font-family:'Roboto';
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.pagecontent{
    margin-top: 90px;
    padding-left: 7px;
    padding-right: 7px;
    height: 100%;
    /* border: 1px dashed black; */
    width: 1200px;
    background-color: #FCFCFC;
    display: flex;
    flex-direction: column;
}
