a{
    all: unset;
    cursor: pointer;
}

hr{
    height: 0px;
    border: none;
    border-bottom:1px solid rgb(248, 248, 248)
}

:root{
    --desiredblue: #204969;
    --desiredgreen: #00AE77;
    --generalshadow: 0px 0px 10px rgb(224, 224, 224);
    --solidone: 1px solid black;
}