.container{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 300px;
    margin-top: auto;
    background-color: rgb(247, 247, 247);
    box-shadow: var(--generalshadow);
}

.container>div{
    display: flex;
    width: 1240px;

}
 
.column{
    padding: 10px;
    flex: 1;
}

.column>h5{
    margin: 1rem;
}

.column:first-child{
    padding-right: 50px;
    padding-left: 20px;
}

.column>input{
    width: 100%;
    height: 35px;
    border-radius: 7px;
    outline: none;
    border: 1px solid rgb(226, 226, 226);
    padding-left: 10px;
    margin-bottom: 10px;
    box-sizing: border-box;
}

.column>button{
    width: 100%;
    height: 35px;
    border-radius: 7px;
    background-color: var(--desiredblue);
    color: white;
    border: none;
    cursor: pointer;
}

.socials{
    display: flex;
    width: 100%;
    padding-right: 140px;
    box-sizing: border-box;
    justify-content: space-between;
}